import ErrorMessage from "../errorMessage/ErrorMessage";
import {Link} from "react-router-dom";

const Error404 = () => {
    return (
        <div>
            <ErrorMessage/>
            <p style={{textAlign: 'center', fontWeight: 'bold', fontSize: '24px'}}>Page not found</p>
            <Link style={{display: 'block', textAlign: 'center', fontWeight: 'bold', fontSize: '30px', marginTop: '30px', textDecoration: 'underline', color: '#9f0013'}} to={'/'}>
                Back to Main page
            </Link>
        </div>
    )
}

export default Error404;