import { Helmet } from 'react-helmet';

import errorGif from './error.gif';

const ErrorMessage = () => {
    return (
        <>
            <Helmet>
                <meta
                    name="description"
                    content="Error 404 Page not found"
                />
                <title>Page not found 404</title>
            </Helmet>
            <img src={errorGif} 
             alt="Error img" 
             style={{
                      display: 'block',
                      margin: '0 auto', 
                      width: '250px', 
                      height: '250px', 
                      objectFit: 'contain'}} />
        </>
    )
}

export default ErrorMessage;